import React, { Component } from "react";
import axios from "axios";
import { Modal, ModalBody, ModalFooter } from "reactstrap";

class Client extends Component {
  /* ------------------------------- properties ------------------------------- */

  state = {
    getProjects: [],
    getClient: [],
    clientName: "",
    clientId: null,
    openEditModal: false
  };

  resetState = {
    clientName: "",
    clientId: null,
    openEditModal: false
  };

  Unmount = false;

  /* ----------------------------- lifecycle hooks ---------------------------- */

  componentDidMount = () => {
    this.getClient();
    this.getProjects();
  };

  componentWillUnmount = () => {
    this.Unmount = true;
  };

  /* ------------------------------ axios method ------------------------------ */
  postClient = () => {
    const { clientName, pay, clientId } = this.state;
    axios({
      url: "https://minit-server-app.herokuapp.com/client",
      method: "POST",
      data: {
        clientName,
        pay,
        id: clientId !== null ? clientId : null
      }
    })
      .then(async res => {
        if (this.Unmount) return;
        await this.getClient();
      })
      .catch(err => console.log(err));
  };

  getClient = () => {
    axios({
      url: "https://minit-server-app.herokuapp.com/client"
    })
      .then(res => {
        if (this.Unmount) return;
        if (!this.state.openEditModal) {
          this.setState({
            getClient: [...res.data],
            ...this.resetState
          });
        } else {
          this.setState({
            getClient: [...res.data]
          });
        }
      })
      .catch(err => console.log(err));
  };

  deleteClient = async (id, mode) => {
    const getProjects = [...this.state.getProjects];
    const newGP = getProjects.filter(item => item.clientId === id);

    newGP.forEach(
      async item => await this.handleDeletePostsAndCreateANewOne(item, mode)
    );

    await axios({
      url: `https://minit-server-app.herokuapp.com/client/${id}`,
      method: "DELETE"
    })
      .then(res => {
        if (this.Unmount) return;
        this.getClient();
        this.setState({ ...this.resetState });
      })
      .catch(err => console.log(err));
  };

  getProjects = () => {
    axios({
      url: "https://minit-server-app.herokuapp.com/projects"
    }).then(res => this.setState({ getProjects: [...res.data] }));
  };

  /* ----------------------------- handle Methods ----------------------------- */

  handleInputs = e => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  handleEditModal = async () => {
    const openEditModal = !this.state.openEditModal;
    if (!openEditModal) {
      this.setState({ ...this.resetState });
    }
    await this.setState({ openEditModal });
  };

  handleEditTask = item => {
    const { clientName, id: clientId } = item;
    this.setState({ clientName, clientId });
  };

  handleEditTaskSubmit = async () => {
    const { clientId } = this.state;

    const getProjects = [...this.state.getProjects];
    const newGP = getProjects.filter(item => item.clientId === clientId);

    await axios({
      url: `https://minit-server-app.herokuapp.com/client/${clientId}`,
      method: "DELETE"
    })
      .then(async res => {
        if ((await newGP.length) > 0) {
          await newGP.forEach(async value => {
            await this.handleDeletePostsAndCreateANewOne(value, "edit");
          });
        }
        await this.postClient();
      })
      .catch(err => console.log(err));

    await this.setState({ ...this.resetState });
  };

  handleDeletePostsAndCreateANewOne = async (item, mode) => {
    const { name, color, budget, projectAccess, id } = item;

    if (mode === "delete") {
      const clientName = "";
      const clientId = null;

      this.setState({ clientName, clientId });
    }

    const { clientName, clientId } = this.state;

    await axios({
      url: `https://minit-server-app.herokuapp.com/projects/${item.id}`,
      method: "DELETE"
    }).then(res => {
      axios({
        url: "https://minit-server-app.herokuapp.com/projects",
        method: "POST",
        data: {
          name,
          color,
          budget,
          projectAccess,
          clientName,
          clientId,
          id
        }
      })
        .then(res => {
          return;
        })
        .catch(err => console.log(err));
    });
  };

  render() {
    return (
      <div className="client-parent h-100 px-3">
        <div className="client-header"></div>

        <div className="client-body container">
          <div className="row justify-content-center align-items-center py-3">
            <div className="col-12 col-md-8 col-lg-7 col-xl-5">
              <div className="d-flex align-items-center">
                <input
                  name="clientName"
                  value={!this.state.openEditModal ? this.state.clientName : ""}
                  onChange={this.handleInputs}
                  type="text"
                  className="form-control"
                  placeholder=""
                />
                <button
                  className="btn btn-success mr-3"
                  onClick={
                    this.state.clientName.length > 0 ? this.postClient : null
                  }
                >
                  
                </button>
              </div>
            </div>
          </div>
          {this.state.getClient.length > 0 && (
            <div className="py-3">
              {this.state.getClient.map(item => (
                <span
                  key={item.id}
                  className="badge badge-secondary ml-2 fs-14 font-weight-normal mb-2"
                >
                  {item.clientName}

                  <i
                    className="fas fs-14 fa-pencil-alt mr-1"
                    onClick={() => {
                      this.handleEditModal();
                      this.handleEditTask(item);
                    }}
                  ></i>

                  <i
                    className="fas fs-14 fa-times mr-1"
                    onClick={() => {
                      this.deleteClient(item.id, "delete");
                    }}
                  ></i>
                </span>
              ))}
            </div>
          )}
        </div>

        <Modal
          isOpen={this.state.openEditModal}
          toggle={this.handleEditModal}
          className="create-modal"
        >
          <ModalBody>
            <div className="container">
              <div className="row justify-content-center">
                <div className="col-12 col-sm-10 col-md-8">
                  <div className="mb-3">
                    <input
                      name="clientName"
                      type="text"
                      className="form-control w-100"
                      placeholder=""
                      value={this.state.clientName}
                      onChange={this.handleInputs}
                    />
                  </div>
                </div>
              </div>
            </div>
          </ModalBody>
          <ModalFooter>
            <div>
              <button
                className="btn btn-padding-orginal btn-secondary"
                onClick={() => this.setState({ ...this.resetState })}
              >
                
              </button>
              <button
                onClick={this.handleEditTaskSubmit}
                className="btn btn-padding-orginal btn-primary mr-2"
              >
                 
              </button>
            </div>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default Client;
