import React, { Component } from "react";
import axios from "axios";
import {
  Popover,
  PopoverBody,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "reactstrap";

class Projects extends Component {
  /* -------------------------- state and properties -------------------------- */
  state = {
    modalToggle: false,
    editProjectModal: false,
    popoverToggle: false,
    getColors: [],
    getProjects: [],
    getPosts: [],
    getClient: [],
    currentColor: "",
    projectName: "",
    projectId: 0,
    projectIndex: 0,
    budget: "",
    color: "",
    clientName: "",
    clientId: null,
    searchName: "",
    searchBudget: "",
    searchCustomer: "",
    projectUnvalidation: false,
    signIn: true,
    admin: false,
    newProjectID: null,
  };

  resetModalAndPopOverAndCreateProjectProperties = {
    modalToggle: false,
    editProjectModal: false,
    popoverToggle: false,
    projectName: "",
    projectId: 0,
    projectIndex: 0,
    budget: "",
    color: "",
    clientName: "",
    clientId: null,
    signIn: true,
    admin: false,
  };

  resetSearchProperties = {
    searchName: "",
    searchBudget: "",
    searchCustomer: "",
  };

  Unmount = false;

  /* ----------------------------- lifecycle hooks ---------------------------- */

  componentDidMount = () => {
    this.getClient();
    this.getPosts();
    this.getColors();
    this.getProjects();
  };

  componentDidUpdate = () => {
    if (!this.state.currentColor.length === 0) {
      this.randomColor();
    }
  };

  componentWillUnmount = () => {
    this.Unmount = true;
  };

  /* ---------------------------------- axios --------------------------------- */

  getClient = () => {
    axios({
      url: "https://minit-server-app.herokuapp.com/client",
    })
      .then((res) => {
        if (this.Unmount) return;
        this.setState({ getClient: [...res.data] });
      })
      .catch((err) => console.log(err));
  };

  getPosts = () => {
    axios({ url: "https://minit-server-app.herokuapp.com/posts" })
      .then((response) => {
        if (this.Unmount) return;
        this.setState({ getPosts: response.data });
      })
      .catch((err) => console.log(err));
  };

  getColors = () => {
    axios({ url: "https://minit-server-app.herokuapp.com/colors" })
      .then((response) => {
        if (this.Unmount) return;
        this.setState({
          getColors: response.data,
        });
      })
      .catch((err) => console.log(err));
  };

  getProjects = () => {
    axios({
      url: "https://minit-server-app.herokuapp.com/projects",
    })
      .then((response) => {
        if (this.Unmount) return;
        this.setState({ getProjects: response.data });
      })
      .catch((err) => console.log(err));
  };

  postProject = async () => {
    const {
      projectName,
      currentColor,
      signIn,
      admin,
      budget,
      projectId,
      clientName,
      clientId,
    } = this.state;

    if (this.state.editProjectModal) {
      await axios({
        url: `https://minit-server-app.herokuapp.com/projects/${projectId}`,
        method: "DELETE",
      })
        .then((res) => {
          return;
        })
        .catch((err) => console.log(err));
    }

    if (this.state.projectName.length > 0) {
      await axios({
        url: "https://minit-server-app.herokuapp.com/projects",
        method: "POST",
        data: {
          name: projectName,
          color: currentColor,
          clientName,
          clientId,
          budget: budget === "" ? 0 : budget,
          projectAccess: {
            admin,
            signIn,
          },
          id: this.state.newProjectID,
        },
      })
        .then((response) => {
          const { searchBudget, searchName, searchCustomer } = this.state;
          if (this.Unmount) return;

          // NOTE  this statements below used if we are searching a project and also made a one project or edit it
          if (
            searchBudget.length > 0 ||
            searchCustomer.length > 0 ||
            searchName.length > 0
          ) {
            if (
              (searchName.length > 0
                ? response.data.name === searchName
                : true) &&
              (searchBudget.length > 0
                ? Number(response.data.budget) === Number(searchBudget)
                : true) &&
              (searchCustomer.length > 0
                ? searchCustomer === response.data.customer
                : true)
            ) {
              // this.state.getProjects.push(response.data);
              this.setState((prevState) => {
                if (this.state.editProjectModal) {
                  prevState.getProjects.splice(this.state.projectIndex, 1);
                }
                prevState.getProjects.push(response.data);
              });
              this.setState({
                ...this.resetModalAndPopOverAndCreateProjectProperties,
              });
              return;
            } else {
              this.getProjects();
              this.setState({
                ...this.resetModalAndPopOverAndCreateProjectProperties,
              });
              return;
            }
          }
          this.getProjects();

          this.handleChangePosts(projectName, projectId, currentColor);

          this.setState({
            ...this.resetModalAndPopOverAndCreateProjectProperties,
          });
        })
        .catch((err) => console.log(err));
    } else {
      this.setState({ projectUnvalidation: true });
      setTimeout(() => {
        this.setState({ projectUnvalidation: false });
      }, 2000);
    }
  };

  /* ---------------- handle Methods --------------- */

  handleChangePosts = (projectName, projectId, currentColor) => {
    const getPosts = [...this.state.getPosts];

    const newGP = getPosts.filter((value) => value.projectId === projectId);

    newGP.forEach(
      async (item) =>
        await this.handleDeletePostsAndCreateANewOne(
          item,
          projectName,
          projectId,
          currentColor
        )
    );
  };

  handleDeletePostsAndCreateANewOne = async (
    item,
    projectName,
    projectId,
    currentColor
  ) => {
    const {
      description,
      projectIndex,
      activityName,
      tagName,
      time,
      date,
      id,
    } = item;

    await axios({
      url: "https://minit-server-app.herokuapp.com/posts",
      method: "POST",
      data: {
        description,
        projectId,
        projectName,
        projectIndex,
        color: currentColor,
        activityName,
        tagName,
        time,
        date,
        id,
      },
    })
      .then((res) => {
        return;
      })
      .catch((err) => console.log(err));
  };

  handleModalToggle = () => {
    const modalToggle = !this.state.modalToggle;
    if (modalToggle) {
      this.randomColor();
      this.setState({ ...this.resetModalAndPopOverAndCreateProjectProperties });
    }
    this.setState({ modalToggle });
  };

  handlePopover = () => {
    const popoverToggle = !this.state.popoverToggle;
    this.setState({ popoverToggle });
  };

  handleInputs = (e) => {
    const { name } = e.currentTarget;
    const { value } = e.target;

    if (name === "clientName") {
      const index = e.target.selectedIndex;
      const item = e.target[index];
      const clientId = Number(item.getAttribute("clientid"));
      this.setState({ clientId });
    }
    this.setState({ [name]: value });
  };

  handleCheckBox = (e) => {
    const { name } = e.target;
    this.setState({ [name]: !this.state[name] });
  };

  handleColorPick = (color) => {
    this.setState({ currentColor: color });
  };

  handleDeletePostsAndDates = (id) => {
    axios({
      url: `https://minit-server-app.herokuapp.com/posts/${id}`,
      method: "DELETE",
    })
      .then((res) => {
        return;
      })
      .catch((err) => console.log(err));

    axios({
      url: `https://minit-server-app.herokuapp.com/date/${id}`,
      method: "DELETE",
    })
      .then((res) => {
        return;
      })
      .catch((err) => console.log(err));
  };

  handleDeleteProject = (id, item) => {
    const getPosts = [...this.state.getPosts];

    const newGP = getPosts.filter((value) => value.projectId === id);

    newGP.forEach((value) => this.handleDeletePostsAndDates(value.id));

    axios({
      url: `https://minit-server-app.herokuapp.com/projects/${id}`,
      method: "DELETE",
    })
      .then((response) => {
        const { searchBudget, searchName, searchCustomer } = this.state;

        if (
          searchBudget.length > 0 ||
          searchCustomer.length > 0 ||
          searchName.length > 0
        ) {
          if (
            (searchName.length > 0 ? item.name === searchName : true) &&
            (searchBudget.length > 0
              ? Number(item.budget) === Number(searchBudget)
              : true)
          ) {
            const getProjects = [...this.state.getProjects];
            const index = getProjects.indexOf(item);

            getProjects.splice(index, 1);
            this.setState({ getProjects });

            return;
          } else {
            return;
          }
        }

        this.getProjects();
        this.setState({ ...this.resetSearchProperties });
      })
      .catch((err) => console.log(err));
  };

  handleSearch = () => {
    const { searchName, searchBudget, searchCustomer } = this.state;

    const regExpSearchName = new RegExp(`${searchName}`, "i");

    const getProjects = this.state.getProjects.filter((item) => {
      if (
        (searchName.length > 0
          ? regExpSearchName.test(item.name)
            ? searchName.length === item.name.length
            : false
          : true) &&
        (searchBudget.length > 0
          ? Number(item.budget) === Number(searchBudget)
          : true) &&
        (searchCustomer.length > 0 ? searchCustomer === item.clientName : true)
      ) {
        return item;
      }
      return 0;
    });

    this.setState({ getProjects });
  };

  handlEditProjects = (
    projectName,
    color,
    budget,
    clientName,
    clientId,
    signIn,
    admin,
    projectId,
    item
  ) => {
    const getProjects = [...this.state.getProjects];
    const projectIndex = getProjects.indexOf(item);

    this.setState({
      projectName,
      color,
      currentColor: color,
      budget,
      clientName,
      clientId,
      signIn,
      admin,
      modalToggle: true,
      editProjectModal: true,
      projectId,
      projectIndex,
      newProjectID: projectId,
    });
  };

  handleResetSearch = () => {
    this.getProjects();
    this.setState({ ...this.resetSearchProperties });
  };
  /* ------------------------------ other Methods ----------------------------- */
  randomColor = async () => {
    const getColors = [...this.state.getColors];
    const number = Math.floor(Math.random() * 20);
    const currentColor = getColors[number];

    const rgb = { ...currentColor };

    await this.setState({ currentColor: rgb.color });
  };

  render() {
    return (
      <React.Fragment>
        <div className="projects h-100">
          <div className="projects-header px-3 d-flex justify-content-between align-items-center">
            <h1 className="h2"></h1>
            <button
              className="btn btn-success btn-project"
              onClick={this.handleModalToggle}
            >
              
            </button>
          </div>

          {/* ---------------------------------- modal --------------------------------- */}
          <Modal
            isOpen={this.state.modalToggle}
            toggle={this.handleModalToggle}
            className="create-modal"
            onClick={() => {
              this.setState({ popoverToggle: false });
            }}
          >
            <ModalHeader toggle={this.handleModalToggle}></ModalHeader>
            <ModalBody>
              <div>
                <div className="d-flex justify-content-between align-items-center">
                  <input
                    className={
                      this.state.projectUnvalidation
                        ? "inputUnvalidation form-control"
                        : "form-control"
                    }
                    type="text"
                    placeholder=" "
                    name="projectName"
                    value={this.state.projectName}
                    onChange={this.handleInputs}
                  />
                  <div
                    className="btn-color pointer bg-white d-flex justify-content-between align-items-center mr-2"
                    id="colorPickPopover"
                  >
                    <div
                      className="m-1 color"
                      style={{
                        backgroundColor: this.state.currentColor,
                      }}
                    ></div>
                    <i className="fas fa-caret-down"></i>
                  </div>
                  {/* -------------------------- Color Select Popover -------------------------- */}

                  <Popover
                    placement="bottom"
                    isOpen={this.state.popoverToggle}
                    target="colorPickPopover"
                    toggle={this.handlePopover}
                  >
                    <PopoverBody className="colorPick-popover-body d-flex flex-wrap justify-content-around ">
                      {this.state.getColors.map((item, index) => {
                        return (
                          <div
                            key={index}
                            className="color pointer"
                            style={{ backgroundColor: item.color }}
                            color={item.color}
                            onClick={() => {
                              this.handleColorPick(item.color);
                            }}
                          ></div>
                        );
                      })}
                    </PopoverBody>
                  </Popover>

                  {/* -------------------------------------------------------------------------- */}
                </div>

                <div>
                  <input
                    className="form-control w-100"
                    type="text"
                    placeholder="  ( )"
                    name="budget"
                    value={this.state.budget}
                    onChange={this.handleInputs}
                  />
                </div>

                <div>
                  <select
                    className="form-control w-100"
                    value={this.state.clientName}
                    name="clientName"
                    onChange={this.handleInputs}
                  >
                    <option value=""></option>
                    {this.state.getClient.length > 0
                      ? this.state.getClient.map((client) => (
                          <option
                            key={client.id}
                            value={client.clientName}
                            clientid={client.id}
                          >
                            {client.clientName}
                          </option>
                        ))
                      : null}
                  </select>
                </div>
              </div>

              <div className="mt-5 container">
                <div className="row">
                  <div className="col-10 mx-auto">
                    <div className="text-center font-weight-bold mb-3">
                       
                    </div>
                    <table className="table">
                      <thead className="project-access-thead">
                        <tr>
                          <th className="w-71px"></th>
                          <th className="w-71px"></th>
                          <th></th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td className="w-71px">
                            <input
                              type="checkbox"
                              name="admin"
                              onChange={this.handleCheckBox}
                              checked={this.state.admin}
                            />
                          </td>
                          <td className="w-71px">
                            <input
                              name="signIn"
                              type="checkbox"
                              checked={this.state.signIn}
                              onChange={this.handleCheckBox}
                            />
                          </td>
                          <td className="text-left">User Name Here</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </ModalBody>
            <ModalFooter>
              <button
                className="btn btn-success btn-project w-auto"
                onClick={this.postProject}
              >
                
              </button>{" "}
              <button
                className="btn btn-danger btn-project w-auto"
                onClick={this.handleModalToggle}
              >
                
              </button>
            </ModalFooter>
          </Modal>
          {/* -------------------------------------------------------------------------- */}

          <div className="projects-search bg-white mx-3 px-3 py-2 d-lg-flex justify-content-lg-between">
            <div className="d-lg-flex align-items-lg-center flex-lg-grow-1">
              <div className="px-3 projects-search-items">
                <input
                  placeholder=""
                  className="form-control w-100 mb-3"
                  type="text"
                  name="searchName"
                  value={this.state.searchName}
                  onChange={this.handleInputs}
                />
              </div>
              <div className="px-3 projects-search-items">
                <input
                  placeholder=" "
                  className="form-control w-100 mb-3"
                  type="text"
                  name="searchBudget"
                  value={this.state.searchBudget}
                  onChange={this.handleInputs}
                />
              </div>
              <div className="px-3 projects-search-items">
                <select
                  value={this.state.searchCustomer}
                  className="form-control w-100 mb-3"
                  name="searchCustomer"
                  onChange={this.handleInputs}
                >
                  <option value=""></option>
                  {this.state.getClient.length > 0
                    ? this.state.getClient.map((client) => (
                        <option
                          key={client.id}
                          value={client.clientName}
                          clientid={client.id}
                        >
                          {client.clientName}
                        </option>
                      ))
                    : null}
                </select>
              </div>
            </div>

            <div className="d-flex justify-content-end align-items-center">
              <div className="d-flex justify-content-between align-items-center pr-3 py-2">
                <button
                  className="btn btn-projects-search"
                  onClick={this.handleSearch}
                >
                  <i className="fas fa-search"></i>
                </button>
                <button
                  className="btn btn-projects-search mr-2"
                  onClick={this.handleResetSearch}
                >
                  <i className="fas fa-times"></i>
                </button>
              </div>
            </div>
          </div>

          {this.state.getProjects.length === 0 ? (
            <div className="p-3 mx-3 fs-12 text-muted">  .</div>
          ) : (
            <div className="table-responsive px-4">
              <table className="table showProjects">
                <thead>
                  <tr>
                    <td></td>
                    <td>  ( )</td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td>
                      <i
                        className="pointer fas fa-sync-alt"
                        onClick={this.handleSearch}
                      ></i>
                    </td>
                  </tr>
                </thead>

                <tbody>
                  {this.state.getProjects.map((item, index) => {
                    return (
                      <tr key={index}>
                        <td className="pointer" style={{ color: item.color }}>
                          {item.name}
                        </td>
                        <td className="pointer iranSansNum">{item.budget}</td>
                        <td className="pointer">{item.clientName}</td>
                        <td>
                          <i
                            className="pointer fas fa-pencil-alt"
                            onClick={() =>
                              this.handlEditProjects(
                                item.name,
                                item.color,
                                item.budget,
                                item.clientName,
                                item.clientId,
                                item.projectAccess.signIn,
                                item.projectAccess.admin,
                                item.id,
                                item
                              )
                            }
                          ></i>
                        </td>
                        <td>
                          <i
                            className="pointer fas fa-times"
                            onClick={() => {
                              this.handleDeleteProject(item.id, item);
                            }}
                          ></i>
                        </td>
                        <td></td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </React.Fragment>
    );
  }
}

export default Projects;
