// FIXME Search box didn't develop
// FIXME group panel didn't develop
import React, { Component } from "react";
import axios from "axios";
import _ from "lodash";
import { Doughnut } from "react-chartjs-2";

class ReportSummary extends Component {
  /* ------------------------------- Properties ------------------------------- */

  state = {
    startSearchDate: this.getNewDate().toLocaleDateString("fa-IR", {
      month: "2-digit",
      day: "2-digit",
      year: "2-digit"
    }),
    endSearchDate: this.getNewDate().toLocaleDateString("fa-IR", {
      month: "2-digit",
      day: "2-digit",
      year: "2-digit"
    }),
    getPosts: [],
    userPostsTimeSum: 0,
    extendUserName: false,
    dateForPanelTimeDashboardChart: []
  };

  /* ---------------------------- lifecycle methods --------------------------- */

  componentDidMount = () => {
    this.getPosts();
  };

  componentWillUnmount = () => {
    this.Unmount = true;
  };

  /* ----------------------------- handle Methods ----------------------------- */

  handleInputs = e => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  handleExtendUserName = () => {
    const extendUserName = !this.state.extendUserName;

    this.setState({ extendUserName });
  };

  /* ------------------------------ chart option ------------------------------ */
  miniChartOptions = () => {
    return {
      tooltips: {
        displayColors: false,
        callbacks: {
          title: (tooltipItem, data) => {
            return data["labels"][tooltipItem[0]["index"]];
          },
          label: (tooltipItem, data) => {
            return this.formatTime(
              data["datasets"][0]["data"][tooltipItem["index"]]
            );
          },
          footer: (tooltipItem, data) => {
            const dataset = data["datasets"][0];

            const total = dataset["data"].reduce(
              (previousValue, currentValue, currentIndex, array) => {
                return previousValue + currentValue;
              }
            );

            const currentValue = dataset["data"][tooltipItem[0]["index"]];

            const percentage = Math.floor((currentValue / total) * 100 + 0.5);

            return percentage + "%";
          }
        }
      },
      layout: {
        padding: {
          left: 15,
          right: 15,
          bottom: 15
        }
      },
      title: {
        display: false
      }
    };
  };

  /* ------------------------------ axios methods ----------------------------- */

  getPosts = () => {
    axios({
      url: "https://minit-server-app.herokuapp.com/posts"
    }).then(res => {
      if (this.Unmount) return;
      this.setState({ getPosts: res.data });
      this.userPostsTimeSum();
      this.filterSameDatePostsAndSum();
    });
  };

  /* ------------------------------ Other Methods ----------------------------- */

  getNewDate() {
    const d = new Date();
    return d;
  }

  filterSameDatePostsAndSum = () => {
    const posts = [...this.state.getPosts];
    const dateArr = [];
    for (let i in posts) {
      dateArr.push(posts[i].projectName);
    }

    const uniqueDate = new Set(dateArr);

    const newDateArr = [...uniqueDate];

    const dataObjectForDashboardChart = {};

    for (let i in newDateArr) {
      dataObjectForDashboardChart[newDateArr[i]] = 0;
    }

    posts.filter(item => {
      return newDateArr.filter(value =>
        item.projectName === value
          ? (dataObjectForDashboardChart[item.projectName] += item.time.count)
          : null
      );
    });

    const dateForPanelTimeDashboardChart = [];
    for (let i in dataObjectForDashboardChart) {
      const trasferDataObjectToAxis = {
        projectName: i,
        count: dataObjectForDashboardChart[i]
      };
      dateForPanelTimeDashboardChart.push(trasferDataObjectToAxis);
    }

    this.setState({ dateForPanelTimeDashboardChart });
  };

  formatTime = secs => {
    var hours = Math.floor(secs / 3600);
    var divisor_for_minutes = secs % 3600;
    var minutes = Math.floor(divisor_for_minutes / 60);
    var seconds = divisor_for_minutes % 60;

    return `${hours < 10 ? "0" + hours : hours}:${
      minutes < 10 ? "0" + minutes : minutes
    }:${seconds < 10 ? "0" + seconds : seconds}`;
  };

  userPostsTimeSum = () => {
    const posts = [...this.state.getPosts];

    const userPostsTimeSum = _.sumBy(posts, item => item.time.count);

    this.setState({ userPostsTimeSum });
  };

  render() {
    return (
      <React.Fragment>
        <div className="report-parent h-100 px-3">
          <div className="report-header"> </div>

          <div className="report-search bg-white mb-3 p-3 d-flex flex-column flex-md-row justify-content-md-between align-items-center">
            <div className="d-flex flex-column flex-sm-row justify-content-start align-items-center flex-wrap flex-md-nowrap">
              <div className="d-flex justify-content-start align-items-center px-3 report-search-items">
                <span className="ml-1 fs-13"></span>
                <input
                  type="text"
                  name="startSearchDate"
                  value={this.state.startSearchDate}
                  onChange={this.handleInputs}
                  className="form-control iranSansNum report-date-input"
                />
                <span className="mx-1 fs-13"></span>
                <input
                  type="text"
                  name="endSearchDate"
                  value={this.state.endSearchDate}
                  onChange={this.handleInputs}
                  className="form-control iranSansNum report-date-input"
                />
              </div>

              <div className="d-flex justify-content-start align-items-center px-3 report-search-items">
                <div className="pointer">
                  <span className="fs-13 ml-2">  </span>
                  <i className="fs-13 fas fa-sort-down"></i>
                </div>
              </div>

              <div className="d-flex justify-content-start align-items-center px-3 report-search-items">
                <div className="pointer">
                  <span className="fs-13 ml-2">  </span>
                  <i className="fs-13 fas fa-sort-down"></i>
                </div>
              </div>

              <div className="d-flex justify-content-start align-items-center px-3 report-search-items">
                <div className="pointer">
                  <span className="fs-13 ml-2">  </span>
                  <i className="fs-13 fas fa-sort-down"></i>
                </div>
              </div>
            </div>

            <div className="mt-3 mt-md-0">
              <button className="btn btn-secondary">
                <i className="fas fa-search"></i>
              </button>
              <button className="btn btn-secondary mr-2">
                <i className="fas fa-times"></i>
              </button>
            </div>
          </div>

          <div className="pb-3 pt-4 d-flex align-items-center container">
            <div className="row w-100">
              <div className="col-12 col-sm-6 col-md-3 d-flex justify-content-center align-items-center">
                <span className="label-sum"></span>
                <span className="total-sum iranSansNum">
                  {this.formatTime(this.state.userPostsTimeSum)}
                </span>
              </div>
            </div>
          </div>
          <div className="container">
            <div className="row">
              <div className="col-12 mb-5 mb-md-0 col-md-8">
                <div className="reportSummary-panel bg-white">
                  <div className="reportSummary-panel-body">
                    <div className="reportSummary-panel-groups py-2 mb-3 d-flex align-items-center">
                      <span onClick={() => this.handleExtendUserName()}>
                        {this.state.extendUserName ? (
                          <i className="far fa-minus-square"></i>
                        ) : (
                          <i className="far fa-plus-square"></i>
                        )}
                      </span>
                      <button className="btn btn-secondary mr-2">
                          / 
                        <i className="fas fa-sort-down mr-2"></i>
                      </button>
                    </div>

                    <div className="reportSummary-panel-body-tabel py-4">
                      <div className="mx-2 mx-sm-4">
                        <div className="d-flex justify-content-between align-items-center">
                          <div>
                            <span onClick={() => this.handleExtendUserName()}>
                              {this.state.extendUserName ? (
                                <i className="far fa-minus-square"></i>
                              ) : (
                                <i className="far fa-plus-square"></i>
                              )}
                            </span>
                            <span className="font-weight-bold mr-2">
                              Kaveh.Karami.KK
                            </span>
                          </div>

                          <div>
                            <span className="font-weight-bold iranSansNum">
                              {this.formatTime(this.state.userPostsTimeSum)}
                            </span>
                          </div>
                        </div>

                        {this.state.extendUserName &&
                          this.state.dateForPanelTimeDashboardChart.map(
                            (item, index) => (
                              <div
                                key={index}
                                className="d-flex justify-content-between align-items-center my-2"
                              >
                                <div>{item.projectName}</div>

                                <div className="iranSansNum">
                                  {this.formatTime(item.count)}
                                </div>
                              </div>
                            )
                          )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col-12 col-md-4">
                {this.state.getPosts.length > 0 ? (
                  <Doughnut
                    data={{
                      labels: ["Kaveh.Karami.KK"],
                      datasets: [
                        {
                          data: [this.state.userPostsTimeSum],
                          backgroundColor: ["#21335E"],
                          borderColor: "#fff",
                          borderWidth: 2
                        }
                      ]
                    }}
                    options={this.miniChartOptions()}
                    legend={{
                      display: false
                    }}
                  />
                ) : (
                  <div className="miniNotFoundDataForChart px-4">
                        
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default ReportSummary;
