import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import Menu from "./components/Menu";
import PanelTime from "./components/PanelTime";
import Projects from "./components/Projects";
import TaskType from "./components/Tasktype";
import Client from "./components/Client";
import Dashboard from "./components/Dashboard";
import ReportSummary from "./components/ReportSummary";
import ReportDetail from "./components/ReportDetail";
import ReportProject from "./components/ReportProject";
import NotFound from "./components/NotFound";

class App extends Component {
  render() {
    return (
      <div className="d-flex h-100">
        <Menu />
        <div className="container-fluid px-0">
          <Switch>
            <Route path="/" component={PanelTime} exact />
            <Route path="/projects" component={Projects} exact />
            <Route path="/tasktype" component={TaskType} exact />
            <Route path="/client" component={Client} exact />
            <Route path="/dashboard" component={Dashboard} exact />
            <Route path="/report/summary" component={ReportSummary} exact />
            <Route path="/report/detail" component={ReportDetail} exact />
            <Route path="/report/project" component={ReportProject} exact />
            <Route component={NotFound} />
          </Switch>
        </div>
      </div>
    );
  }
}

export default App;
