import React, { Component } from "react";
import axios from "axios";
import { Modal, ModalBody, ModalFooter } from "reactstrap";

class TaskType extends Component {
  /* ------------------------------- properties ------------------------------- */

  state = {
    getPosts: [],
    getTasktype: [],
    activityName: "",
    activityId: null,
    pay: false,
    openEditModal: false
  };

  resetState = {
    activityName: "",
    activityId: null,
    pay: false,
    openEditModal: false
  };

  Unmount = false;

  /* ----------------------------- lifecycle hooks ---------------------------- */

  componentDidMount = () => {
    this.getTasktype();
    this.getPosts();
  };

  componentWillUnmount = () => {
    this.Unmount = true;
  };

  /* ------------------------------ axios method ------------------------------ */
  postTasktype = () => {
    const { activityName, pay, activityId } = this.state;
    axios({
      url: "https://minit-server-app.herokuapp.com/tasktype",
      method: "POST",
      data: {
        activityName,
        pay,
        id: activityId !== null ? activityId : null
      }
    })
      .then(async res => {
        if (this.Unmount) return;
        await this.getTasktype();
      })
      .catch(err => console.log(err));
  };

  getTasktype = () => {
    axios({
      url: "https://minit-server-app.herokuapp.com/tasktype"
    })
      .then(res => {
        if (this.Unmount) return;
        if (!this.state.openEditModal) {
          this.setState({
            getTasktype: [...res.data],
            ...this.resetState
          });
        } else {
          this.setState({
            getTasktype: [...res.data]
          });
        }
      })
      .catch(err => console.log(err));
  };

  deleteTasktype = async (id, mode) => {
    const getPosts = [...this.state.getPosts];
    const newGP = getPosts.filter(item => item.activityId === id);

    newGP.forEach(
      async item => await this.handleDeletePostsAndCreateANewOne(item, mode)
    );

    await axios({
      url: `https://minit-server-app.herokuapp.com/tasktype/${id}`,
      method: "DELETE"
    })
      .then(res => {
        if (this.Unmount) return;
        this.getTasktype();
        this.setState({ ...this.resetState });
      })
      .catch(err => console.log(err));
  };

  getPosts = () => {
    axios({
      url: "https://minit-server-app.herokuapp.com/posts"
    }).then(res => this.setState({ getPosts: [...res.data] }));
  };

  /* ----------------------------- handle Methods ----------------------------- */

  handleInputs = e => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  handleCheckbox = e => {
    const { name } = e.target;
    this.setState({ [name]: !this.state[name] });
  };
  handleEditModal = async () => {
    const openEditModal = !this.state.openEditModal;
    if (!openEditModal) {
      this.setState({ ...this.resetState });
    }
    await this.setState({ openEditModal });
  };

  handleEditTask = item => {
    const { activityName, pay, id: activityId } = item;
    this.setState({ activityName, pay, activityId });
  };

  handleEditTaskSubmit = async () => {
    const { activityId } = this.state;

    const getPosts = [...this.state.getPosts];
    const newGP = getPosts.filter(item => item.activityId === activityId);

    await axios({
      url: `https://minit-server-app.herokuapp.com/tasktype/${activityId}`,
      method: "DELETE"
    })
      .then(async res => {
        if ((await newGP.length) > 0) {
          await newGP.forEach(async value => {
            await this.handleDeletePostsAndCreateANewOne(value, "edit");
          });
        }
        await this.postTasktype();
      })
      .catch(err => console.log(err));

    await this.setState({ ...this.resetState });
  };

  handleDeletePostsAndCreateANewOne = async (item, mode) => {
    const {
      description,
      projectIndex,
      projectName,
      projectId,
      color,
      tagName,
      time,
      date,
      id
    } = item;

    if (mode === "delete") {
      const activityName = "";
      const activityId = null;

      this.setState({ activityName, activityId });
    }

    const { activityName, activityId } = this.state;

    await axios({
      url: `https://minit-server-app.herokuapp.com/posts/${item.id}`,
      method: "DELETE"
    }).then(res => {
      axios({
        url: "https://minit-server-app.herokuapp.com/posts",
        method: "POST",
        data: {
          description,
          projectId,
          projectName,
          projectIndex,
          color,
          activityName,
          activityId,
          tagName,
          time,
          date,
          id
        }
      })
        .then(res => {
          return;
        })
        .catch(err => console.log(err));
    });
  };

  render() {
    return (
      <div className="tasktype-parent h-100 px-3">
        <div className="tasktype-header"> </div>

        <div className="tasktype-body container">
          <div className="row justify-content-center align-items-center py-3">
            <div className="col-12 col-md-8 col-lg-7 col-xl-5">
              <div className="d-flex align-items-center">
                <input
                  name="activityName"
                  value={
                    !this.state.openEditModal ? this.state.activityName : ""
                  }
                  onChange={this.handleInputs}
                  type="text"
                  className="form-control"
                  placeholder=""
                />
                <button
                  className="btn btn-success mr-3"
                  onClick={
                    this.state.activityName.length > 0
                      ? this.postTasktype
                      : null
                  }
                >
                  
                </button>
              </div>

              <div className="d-flex align-items-center">
                <input
                  name="pay"
                  onChange={this.handleCheckbox}
                  type="checkbox"
                  checked={!this.state.openEditModal && this.state.pay}
                />
                <span className="mr-1"></span>
              </div>
            </div>
          </div>
          {this.state.getTasktype.length > 0 && (
            <div className="py-3">
              {this.state.getTasktype.map(item => (
                <span
                  key={item.id}
                  className="badge badge-secondary ml-2 fs-14 font-weight-normal mb-2"
                >
                  {item.activityName}

                  <i
                    className="fas fs-14 fa-pencil-alt mr-1"
                    onClick={() => {
                      this.handleEditModal();
                      this.handleEditTask(item);
                    }}
                  ></i>

                  <i
                    className="fas fs-14 fa-times mr-1"
                    onClick={() => {
                      this.deleteTasktype(item.id, "delete");
                    }}
                  ></i>
                </span>
              ))}
            </div>
          )}
        </div>

        <Modal
          isOpen={this.state.openEditModal}
          toggle={this.handleEditModal}
          className="create-modal"
        >
          <ModalBody>
            <div className="container">
              <div className="row justify-content-center">
                <div className="col-12 col-sm-10 col-md-8">
                  <div className="mb-3">
                    <input
                      name="activityName"
                      type="text"
                      className="form-control w-100"
                      placeholder=""
                      value={this.state.activityName}
                      onChange={this.handleInputs}
                    />
                  </div>
                  <div className="d-flex align-items-center">
                    <input
                      name="pay"
                      onChange={this.handleCheckbox}
                      type="checkbox"
                      checked={this.state.pay}
                    />
                    <span className="mr-1"></span>
                  </div>
                </div>
              </div>
            </div>
          </ModalBody>
          <ModalFooter>
            <div>
              <button
                className="btn btn-padding-orginal btn-secondary"
                onClick={() => this.setState({ ...this.resetState })}
              >
                
              </button>
              <button
                onClick={this.handleEditTaskSubmit}
                className="btn btn-padding-orginal btn-primary mr-2"
              >
                 
              </button>
            </div>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default TaskType;
