import React from "react";
import { Link, Route } from "react-router-dom";

export const CustomMenu = props => (
  <Route
    path={props.path}
    exact={props.exact}
    children={({ match }) =>
      match ? (
        <Link
          to={props.path}
          className="menu-item-active list-group-item d-flex justify-content-center align-items-center"
        >
          {props.label}
        </Link>
      ) : (
        <Link
          to={props.path}
          className="menu-item-other list-group-item d-flex justify-content-center align-items-center"
        >
          {props.label}
        </Link>
      )
    }
  />
);

export const CustomDropdownMenu = props => (
  <Route
    path={props.path}
    exact={props.exact}
    children={({ match }) =>
      match ? (
        <Link
          to={props.path}
          className="menu-item-dropdown-active list-group-item d-flex justify-content-center align-items-center"
        >
          {props.label}
        </Link>
      ) : (
        <Link
          to={props.path}
          className="menu-item-dropdown-other list-group-item d-flex justify-content-center align-items-center"
        >
          {props.label}
        </Link>
      )
    }
  />
);
