import React, { Component } from "react";
import { CustomMenu, CustomDropdownMenu } from "./MenuComponents/CustomMenu";
class Menu extends Component {
  state = { reportMode: "/report/summary" };

  changeReportMode = name => {
    this.setState({ reportMode: name });
  };

  render() {
    return (
      <div className="p-lg-2 pb-lg-0 menu-parent">
        <div className="menu d-flex flex-column align-items-center min-vh-100 ">
          <div className="menu-panel">
            <div className="nav-logo"></div>
          </div>

          <div className="nav-item list-group align-items-lg-start">
            <CustomMenu
              path={"/"}
              exact
              label={
                <div className="d-flex justify-content-center align-items-center justify-content-lg-around align-items-lg-center">
                  <i className="far fa-clock pr-lg-2"></i>
                  <span className="d-none d-lg-inline px-lg-3"> </span>
                </div>
              }
            />
            <CustomMenu
              path={"/dashboard"}
              exact
              label={
                <div className="d-flex justify-content-center align-items-center justify-content-lg-around align-items-lg-center">
                  <i className="far fa-chart-bar pr-lg-2"></i>
                  <span className="d-none d-lg-inline px-lg-3"></span>
                </div>
              }
            />
            <div className="position-relative menu-reports-dropdown">
              <CustomMenu
                path={this.state.reportMode}
                exact
                label={
                  <div className="d-flex justify-content-center align-items-center justify-content-lg-around align-items-lg-center ">
                    <i className="fas fa-list-ul pr-lg-2"></i>
                    <span className="d-none d-lg-inline px-lg-3"></span>
                  </div>
                }
              />

              <div className="menu-reports-dropdown-content d-flex flex-column align-items-start justify-content-around p-3">
                <div onClick={() => this.changeReportMode("/report/summary")}>
                  <CustomDropdownMenu
                    path={"/report/summary"}
                    exact
                    label={" "}
                  />
                </div>
                <div onClick={() => this.changeReportMode("/report/detail")}>
                  <CustomDropdownMenu
                    path={"/report/detail"}
                    exact
                    label={" "}
                  />
                </div>
                <div onClick={() => this.changeReportMode("/report/project")}>
                  <CustomDropdownMenu
                    path={"/report/project"}
                    exact
                    label={" "}
                  />
                </div>
              </div>
            </div>

            <div className="menu-seperator">
              <span className="d-none d-lg-block"></span>
            </div>

            <CustomMenu
              path={"/workspace"}
              exact
              label={
                <div className="d-flex justify-content-center align-items-center justify-content-lg-around align-items-lg-center">
                  <i className="fas fa-suitcase pr-lg-2"></i>
                  <span className="d-none d-lg-inline px-lg-3"> </span>
                </div>
              }
            />
            <CustomMenu
              path={"/projects"}
              exact
              label={
                <div className="d-flex justify-content-center align-items-center justify-content-lg-around align-items-lg-center">
                  <i className="fas fa-folder pr-lg-2"></i>
                  <span className="d-none d-lg-inline px-lg-3"></span>
                </div>
              }
            />
            <CustomMenu
              path={"/client"}
              exact
              label={
                <div className="d-flex justify-content-center align-items-center justify-content-lg-around align-items-lg-center">
                  <i className="fas fa-user-circle pr-lg-2"></i>
                  <span className="d-none d-lg-inline px-lg-3"></span>
                </div>
              }
            />
            <CustomMenu
              path={"/tasktype"}
              exact
              label={
                <div className="d-flex justify-content-center align-items-center justify-content-lg-around align-items-lg-center">
                  <i className="fas fa-bars pr-lg-2"></i>
                  <span className="d-none d-lg-inline px-lg-3"> </span>
                </div>
              }
            />
            <CustomMenu
              path={"/team"}
              exact
              label={
                <div className="d-flex justify-content-center align-items-center justify-content-lg-around align-items-lg-center">
                  <i className="fas fa-users pr-lg-1"></i>
                  <span className="d-none d-lg-inline px-lg-3"></span>
                </div>
              }
            />
            <CustomMenu
              path={"/help"}
              exact
              label={
                <div className="d-flex justify-content-center align-items-center justify-content-lg-around align-items-lg-center">
                  <i className="fas fa-info-circle pr-lg-2"></i>
                  <span className="d-none d-lg-inline px-lg-3"></span>
                </div>
              }
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Menu;
