// FIXME Search box didn't develop
import React, { Component } from "react";
import { Bar, Doughnut } from "react-chartjs-2";
import axios from "axios";
import moment from "moment-jalaali";
import _ from "lodash";

class Dashboard extends Component {
  /* ------------------------------- properties ------------------------------- */
  state = {
    startSearchDate: this.getNewDate().toLocaleDateString("fa-IR", {
      month: "2-digit",
      day: "2-digit",
      year: "2-digit"
    }),
    endSearchDate: this.getNewDate().toLocaleDateString("fa-IR", {
      month: "2-digit",
      day: "2-digit",
      year: "2-digit"
    }),
    panelHeaderMode: "daily",
    getPosts: [],
    dateForPanelTimeDashboardChartForWhile: [],
    daysInMonth: [],
    dailyChartColor: [],
    monthlyChartColor: [],
    projectsTimeChartColor: [],
    activityTimeChartColor: [],
    totalSum: 0,
    weeksInMonth: [],
    dateForPanelTimeDashboardWeekChart: [],
    monthsInYear: [],
    dateForPanelTimeDashboardMonthChart: [],
    titleText: "  ",
    projectsLabels: [],
    projectsCount: [],
    userPostsTimeSum: 0,
    activityLabels: [],
    activityCount: []
  };

  Unmount = false;

  /* ---------------------------- lifecycle methods --------------------------- */

  componentDidMount = () => {
    this.getPosts();

    this.handleDate();

    this.handleWeekDate();

    this.handleMonthDate();
  };

  componentWillUnmount = () => {
    this.Unmount = true;
  };

  /* ----------------------------- handle methods ----------------------------- */

  handleInputs = e => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  handlePanelHeaderItemSelect = name => {
    this.setState({ panelHeaderMode: name });
  };

  handleDate = () => {
    const daysInMonth = [];
    // NOTE set arg in the moment for search box
    const monthDate = moment()
      .locale("en")
      .startOf("jMonth");

    _.times(monthDate.daysInMonth(), () => {
      daysInMonth.push(monthDate.format("jYYYY/jMM/jDD"));
      monthDate.add(1, "day");
    });

    this.setState({ daysInMonth });
  };

  handleWeekDate = () => {
    const weeksInMonth = [];

    // NOTE set arg in the moment for search box
    let start = moment()
      .locale("en")
      .startOf("jMonth")
      .format("jDD");

    let end = moment()
      .locale("en")
      .endOf("jMonth")
      .format("jDD");

    let weeks = (end - start + 1) / 7;

    weeks = Math.ceil(weeks);

    const monthDate = moment()
      .locale("en")
      .startOf("jMonth");

    _.times(weeks, () => {
      weeksInMonth.push(monthDate.format("jYYYY/jMM/jDD"));
      monthDate.add(1, "week");
    });

    const weeksInMonthLentgh = weeksInMonth.length;

    // FIXME trim and clean code here
    weeksInMonth.forEach((value, index) => {
      let startWeek = moment()
        .locale("en")
        .startOf("jMonth")
        .format("jYYYY/jMM/jDD");

      let endWeek = moment()
        .locale("en")
        .endOf("jMonth")
        .format("jYYYY/jMM/jDD");

      const newVal = value.split("/");
      if (newVal[2] >= start && index === 0) {
        weeksInMonth[index] = `${startWeek}-${value}`;
      } else {
        const seprateArr = weeksInMonth[index - 1].split("-");
        const beforedays = seprateArr[1].split("/");
        beforedays[2] = Number(beforedays[2]) + 1;
        if (beforedays[2] < 10) {
          beforedays[2] = `0${beforedays[2]}`;
        }
        const joinTogether = beforedays.join("/");
        weeksInMonth[index] = `${joinTogether}-${value}`;
      }

      if (newVal[2] <= end && index === weeksInMonthLentgh - 1) {
        const seprateArr = weeksInMonth[index].split("-");
        const beforedays = seprateArr[1].split("/");
        beforedays[2] = Number(beforedays[2]) + 1;
        const joinTogether = beforedays.join("/");
        weeksInMonth[weeksInMonthLentgh] = `${joinTogether}-${endWeek}`;
      }
    });

    this.setState({ weeksInMonth });
  };

  handleMonthDate = () => {
    // NOTE set arg in the moment for search box
    const monthsInYear = [];

    const monthDate = moment()
      .locale("en")
      .startOf("jYear");

    _.times(12, () => {
      monthsInYear.push(monthDate.format("jYYYY/jMM"));
      monthDate.add(1, "jMonth");
    });

    this.setState({ monthsInYear });
  };

  handleTotalSum = () => {
    const data = [];
    if (this.state.panelHeaderMode === "daily") {
      data.push(...this.state.dateForPanelTimeDashboardChartForWhile);
    } else if (this.state.panelHeaderMode === "weekly") {
      data.push(...this.state.dateForPanelTimeDashboardWeekChart);
    } else {
      data.push(...this.state.dateForPanelTimeDashboardMonthChart);
    }

    const totalSum = _.sumBy(data, "y");

    return totalSum;
  };

  /* ------------------------------ axios methods ----------------------------- */

  getPosts = () => {
    axios({
      url: "https://minit-server-app.herokuapp.com/posts"
    }).then(res => {
      if (this.Unmount) return;
      this.setState({ getPosts: res.data });
      this.projectsFilterDateAndSum();
      this.activityFilterDateAndSum();
      this.filterSameDatePostsAndSum();
      this.userPostsTimeSum();
      this.monthlyFilterDateAndSum();
    });
  };

  /* ------------------------------ chart options ----------------------------- */
  mainChartOptions = () => {
    return {
      scales: {
        yAxes: [
          {
            ticks: {
              stepSize: 2000,
              beginAtZero: true,
              callback: (label, index, labels) => {
                return this.formatTime(label);
              }
            }
          }
        ]
      },
      tooltips: {
        displayColors: false,
        callbacks: {
          title: (tooltipItem, data) => {
            return data["datasets"][0]["data"][tooltipItem[0]["index"]]["x"];
          },
          label: (tooltipItem, data) => {
            return this.formatTime(tooltipItem.yLabel);
          }
        }
      },
      layout: {
        padding: {
          left: 15,
          right: 15,
          bottom: 15
        }
      },
      title: {
        display: true,
        text: this.state.titleText,
        fontFamily: "iranSans",
        fontSize: 16,
        padding: 15
      }
    };
  };

  miniChartOptions = titleText => {
    return {
      tooltips: {
        displayColors: false,
        callbacks: {
          title: (tooltipItem, data) => {
            return data["labels"][tooltipItem[0]["index"]];
          },
          label: (tooltipItem, data) => {
            return this.formatTime(
              data["datasets"][0]["data"][tooltipItem["index"]]
            );
          },
          footer: (tooltipItem, data) => {
            const dataset = data["datasets"][0];

            const total = dataset["data"].reduce(
              (previousValue, currentValue, currentIndex, array) => {
                return previousValue + currentValue;
              }
            );

            const currentValue = dataset["data"][tooltipItem[0]["index"]];

            const percentage = Math.floor((currentValue / total) * 100 + 0.5);

            return percentage + "%";
          }
        }
      },
      layout: {
        padding: {
          left: 15,
          right: 15,
          bottom: 15
        }
      },
      title: {
        display: true,
        text: titleText,
        fontFamily: "iranSans",
        fontSize: 16,
        padding: 15
      }
    };
  };
  /* ------------------------------ other methods ----------------------------- */

  getNewDate() {
    const d = new Date();
    return d;
  }

  filterSameDatePostsAndSum = () => {
    const posts = [...this.state.getPosts];
    const dateArr = [];
    for (let i in posts) {
      dateArr.push(posts[i].date);
    }

    const uniqueDate = new Set(dateArr);

    const newDateArr = [...uniqueDate];

    const dataObjectForDashboardChart = {};

    for (let i in newDateArr) {
      dataObjectForDashboardChart[newDateArr[i]] = 0;
    }

    posts.filter(item => {
      return newDateArr.filter(value =>
        item.date === value
          ? (dataObjectForDashboardChart[item.date] += item.time.count)
          : null
      );
    });

    const dateForPanelTimeDashboardChart = [];
    for (let i in dataObjectForDashboardChart) {
      const trasferDataObjectToAxis = {
        x: i,
        y: dataObjectForDashboardChart[i]
      };
      dateForPanelTimeDashboardChart.push(trasferDataObjectToAxis);
    }

    const daysInMonth = [...this.state.daysInMonth];
    const dateForPanelTimeDashboardChartForWhile = [];
    daysInMonth.filter(item => {
      return dateForPanelTimeDashboardChart.filter(value =>
        item === value.x
          ? dateForPanelTimeDashboardChartForWhile.push(value)
          : null
      );
    });

    this.setState({ dateForPanelTimeDashboardChartForWhile });

    this.generateBgColorForChart("dailyChartColor");
  };

  weeklyFilterDateAndSum = () => {
    const posts = [...this.state.getPosts];
    const weeksDateArr = [...this.state.weeksInMonth];
    const dateArr = [];
    for (let i in posts) {
      dateArr.push(posts[i].date);
    }

    const dataObjectForDashboardChart = {};

    for (let i in weeksDateArr) {
      dataObjectForDashboardChart[weeksDateArr[i]] = 0;
    }

    weeksDateArr.filter(item => {
      const newItem = item;
      const itemSeprate = newItem.split("-");
      const itemStart = itemSeprate[0];
      const itemEnd = itemSeprate[1];
      const itemStartSeprate = itemStart.split("/");
      const itemEndSeprate = itemEnd.split("/");

      const itemYear = itemStartSeprate[0];
      const itemMonth = itemStartSeprate[1];

      const itemStartDay = itemStartSeprate[2];
      const itemEndDay = itemEndSeprate[2];

      return posts.filter(value => {
        const newValue = value.date;

        const valueSeprate = newValue.split("/");

        const valueYear = valueSeprate[0];
        const valueMonth = valueSeprate[1];
        const valueDay = valueSeprate[2];

        if (
          valueDay <= itemEndDay &&
          valueDay >= itemStartDay &&
          valueMonth === itemMonth &&
          valueYear === itemYear
        ) {
          dataObjectForDashboardChart[item] += value.time.count;
        }

        return 1;
      });
    });

    const dateForPanelTimeDashboardWeekChart = [];
    for (let i in dataObjectForDashboardChart) {
      const trasferDataObjectToAxis = {
        x: i,
        y: dataObjectForDashboardChart[i]
      };
      dateForPanelTimeDashboardWeekChart.push(trasferDataObjectToAxis);
    }

    this.setState({ dateForPanelTimeDashboardWeekChart });
  };

  monthlyFilterDateAndSum = () => {
    const posts = [...this.state.getPosts];
    const monthsDateArr = [...this.state.monthsInYear];
    const dateArr = [];
    for (let i in posts) {
      dateArr.push(posts[i].date);
    }

    const dataObjectForDashboardChart = {};

    for (let i in monthsDateArr) {
      dataObjectForDashboardChart[monthsDateArr[i]] = 0;
    }

    monthsDateArr.filter(item => {
      const newItem = item;
      const itemSeprate = newItem.split("/");
      const itemYear = itemSeprate[0];
      const itemMonth = itemSeprate[1];

      return posts.filter(value => {
        const newValue = value.date;

        const valueSeprate = newValue.split("/");

        const valueYear = valueSeprate[0];
        const valueMonth = valueSeprate[1];

        if (valueMonth === itemMonth && valueYear === itemYear) {
          dataObjectForDashboardChart[item] += value.time.count;
        }

        return 1;
      });
    });

    const dateForPanelTimeDashboardMonthChart = [];
    for (let i in dataObjectForDashboardChart) {
      const trasferDataObjectToAxis = {
        x: i,
        y: dataObjectForDashboardChart[i]
      };
      dateForPanelTimeDashboardMonthChart.push(trasferDataObjectToAxis);
    }

    this.setState({ dateForPanelTimeDashboardMonthChart });

    this.generateBgColorForChart("monthlyChartColor");
  };

  projectsFilterDateAndSum = () => {
    const posts = [...this.state.getPosts];
    const projectsArr = [];
    for (let i in posts) {
      projectsArr.push(posts[i].projectName);
    }

    const uniqueDate = new Set(projectsArr);

    const newProjectsArr = [...uniqueDate];

    const dataObjectForDashboardChart = {};

    for (let i in newProjectsArr) {
      dataObjectForDashboardChart[newProjectsArr[i]] = 0;
    }

    posts.filter(item => {
      return newProjectsArr.filter(value =>
        item.projectName === value
          ? (dataObjectForDashboardChart[item.projectName] += item.time.count)
          : null
      );
    });

    const projectsLabels = [];
    const projectsCount = [];
    for (let i in dataObjectForDashboardChart) {
      projectsLabels.push(i);
      projectsCount.push(dataObjectForDashboardChart[i]);
    }

    this.setState({ projectsLabels, projectsCount });

    this.generateBgColorForChart("projectsTimeChartColor");
  };

  activityFilterDateAndSum = () => {
    const posts = [...this.state.getPosts];
    const activityArr = [];
    for (let i in posts) {
      activityArr.push(posts[i].activityName);
    }

    const uniqueDate = new Set(activityArr);

    const newActivityArr = [...uniqueDate];

    const dataObjectForDashboardChart = {};

    for (let i in newActivityArr) {
      if (newActivityArr[i] === "") {
        dataObjectForDashboardChart["  "] = 0;
      } else {
        dataObjectForDashboardChart[newActivityArr[i]] = 0;
      }
    }

    posts.filter(item => {
      return newActivityArr.filter(value =>
        item.activityName === value
          ? item.activityName === ""
            ? (dataObjectForDashboardChart["  "] +=
                item.time.count)
            : (dataObjectForDashboardChart[item.activityName] +=
                item.time.count)
          : null
      );
    });

    const activityLabels = [];
    const activityCount = [];
    for (let i in dataObjectForDashboardChart) {
      activityLabels.push(i);
      activityCount.push(dataObjectForDashboardChart[i]);
    }

    this.setState({ activityLabels, activityCount });

    this.generateBgColorForChart("activityTimeChartColor");
  };

  userPostsTimeSum = () => {
    const posts = [...this.state.getPosts];

    const userPostsTimeSum = _.sumBy(posts, item => item.time.count);

    this.setState({ userPostsTimeSum });
  };

  formatTime = secs => {
    var hours = Math.floor(secs / 3600);
    var divisor_for_minutes = secs % 3600;
    var minutes = Math.floor(divisor_for_minutes / 60);
    var seconds = divisor_for_minutes % 60;

    return `${hours < 10 ? "0" + hours : hours}:${
      minutes < 10 ? "0" + minutes : minutes
    }:${seconds < 10 ? "0" + seconds : seconds}`;
  };

  randomColor = () => {
    const r = Math.floor(Math.random() * 255);
    const g = Math.floor(Math.random() * 255);
    const b = Math.floor(Math.random() * 255);
    return `rgb(${r},${g},${b})`;
  };

  generateBgColorForChart = name => {
    const chartName = name;
    const data = [];
    if (name === "dailyChartColor") {
      data.push(...this.state.dateForPanelTimeDashboardChartForWhile);
    } else if (name === "monthlyChartColor") {
      data.push(...this.state.dateForPanelTimeDashboardMonthChart);
    } else if (name === "projectsTimeChartColor") {
      data.push(...this.state.projectsLabels);
    } else if (name === "activityTimeChartColor") {
      data.push(...this.state.activityLabels);
    }

    const randomColor = [];
    for (let i = 0; i < data.length; i++) {
      const color = this.randomColor();

      randomColor.push(color);
    }

    while (name === "monthly" && randomColor.length < 12) {
      const color = this.randomColor();

      randomColor.push(color);
    }

    this.setState({ [chartName]: randomColor });
  };

  render() {
    return (
      <React.Fragment>
        <div className="dashboard-parent h-100 px-3">
          <div className="dashboard-header"></div>

          <div className="dashboard-search bg-white mb-3 p-3 d-flex flex-column flex-md-row justify-content-md-between align-items-center">
            <div className="d-flex flex-column flex-sm-row justify-content-start align-items-center flex-wrap flex-md-nowrap">
              <div className="d-flex justify-content-start align-items-center px-3 dashboard-search-items">
                <span className="ml-1 fs-13"></span>
                <input
                  type="text"
                  name="startSearchDate"
                  value={this.state.startSearchDate}
                  onChange={this.handleInputs}
                  className="form-control iranSansNum dashboard-date-input"
                />
                <span className="mx-1 fs-13"></span>
                <input
                  type="text"
                  name="endSearchDate"
                  value={this.state.endSearchDate}
                  onChange={this.handleInputs}
                  className="form-control iranSansNum dashboard-date-input"
                />
              </div>

              <div className="d-flex justify-content-start align-items-center px-3 dashboard-search-items">
                <div className="pointer">
                  <span className="fs-13 ml-2">  </span>
                  <i className="fs-13 fas fa-sort-down"></i>
                </div>
              </div>

              <div className="d-flex justify-content-start align-items-center px-3 dashboard-search-items">
                <div className="pointer">
                  <span className="fs-13 ml-2">  </span>
                  <i className="fs-13 fas fa-sort-down"></i>
                </div>
              </div>

              <div className="d-flex justify-content-start align-items-center px-3 dashboard-search-items">
                <div className="pointer">
                  <span className="fs-13 ml-2">  </span>
                  <i className="fs-13 fas fa-sort-down"></i>
                </div>
              </div>
            </div>

            <div className="mt-3 mt-md-0">
              <button className="btn btn-secondary">
                <i className="fas fa-search"></i>
              </button>
              <button className="btn btn-secondary mr-2">
                <i className="fas fa-times"></i>
              </button>
            </div>
          </div>

          <div className="pb-3 pt-4 d-flex align-items-center container">
            <div className="row w-100">
              <div className="col-12 col-sm-6 col-md-3 d-flex justify-content-center align-items-center">
                <span className="label-sum"></span>
                <span className="total-sum iranSansNum">
                  {this.formatTime(this.handleTotalSum())}
                </span>
              </div>
            </div>
          </div>

          <div className="panel bg-white mb-5 mt-4 mx-auto">
            <div className="d-flex justify-content-start align-items-center">
              <span
                className={
                  this.state.panelHeaderMode === "daily"
                    ? "panel-header-item panel-header-item-select"
                    : "panel-header-item"
                }
                onClick={() => {
                  this.handlePanelHeaderItemSelect("daily");
                  this.setState({ titleText: "  " });
                }}
              >
                
              </span>
              <span
                className={
                  this.state.panelHeaderMode === "weekly"
                    ? "panel-header-item panel-header-item-select"
                    : "panel-header-item"
                }
                onClick={() => {
                  this.handlePanelHeaderItemSelect("weekly");
                  this.setState({ titleText: "  " });
                  this.weeklyFilterDateAndSum();
                }}
              >
                
              </span>
              <span
                className={
                  this.state.panelHeaderMode === "monthly"
                    ? "panel-header-item panel-header-item-select"
                    : "panel-header-item"
                }
                onClick={() => {
                  this.handlePanelHeaderItemSelect("monthly");
                  this.setState({ titleText: "  " });
                  this.monthlyFilterDateAndSum();
                }}
              >
                
              </span>
            </div>
            <div>
              {this.state.panelHeaderMode === "daily" &&
                (this.state.dateForPanelTimeDashboardChartForWhile.length >
                0 ? (
                  <Bar
                    height={
                      window.outerWidth < 576
                        ? Math.floor(window.outerHeight * 0.575)
                        : null
                    }
                    data={{
                      labels: [...this.state.daysInMonth],
                      datasets: [
                        {
                          data: [
                            ...this.state.dateForPanelTimeDashboardChartForWhile
                          ],
                          backgroundColor: [...this.state.dailyChartColor],
                          borderColor: "#fff",
                          borderWidth: 2
                        }
                      ]
                    }}
                    options={this.mainChartOptions()}
                    legend={{
                      display: false
                    }}
                  />
                ) : (
                  <div className="notFoundDataForChart">
                        
                  </div>
                ))}

              {this.state.panelHeaderMode === "weekly" &&
                (this.state.dateForPanelTimeDashboardWeekChart.length > 0 ? (
                  <Bar
                    height={
                      window.outerWidth < 576
                        ? Math.floor(window.outerHeight * 0.575)
                        : null
                    }
                    data={{
                      labels: [...this.state.weeksInMonth],
                      datasets: [
                        {
                          data: [
                            ...this.state.dateForPanelTimeDashboardWeekChart
                          ],
                          backgroundColor: [
                            "#D32F2F",
                            "#F57C00",
                            "#FFA000",
                            "#00796B",
                            "#1976D2",
                            "#21335E"
                          ],
                          borderColor: "#fff",
                          borderWidth: 2
                        }
                      ]
                    }}
                    options={this.mainChartOptions()}
                    legend={{
                      display: false
                    }}
                  />
                ) : (
                  <div className="notFoundDataForChart">
                        
                  </div>
                ))}

              {this.state.panelHeaderMode === "monthly" &&
                (this.state.dateForPanelTimeDashboardMonthChart.length > 0 ? (
                  <Bar
                    height={
                      window.outerWidth < 576
                        ? Math.floor(window.outerHeight * 0.575)
                        : null
                    }
                    data={{
                      labels: [...this.state.monthsInYear],
                      datasets: [
                        {
                          data: [
                            ...this.state.dateForPanelTimeDashboardMonthChart
                          ],
                          backgroundColor: [...this.state.monthlyChartColor],
                          borderColor: "#fff",
                          borderWidth: 2
                        }
                      ]
                    }}
                    options={this.mainChartOptions()}
                    legend={{
                      display: false
                    }}
                  />
                ) : (
                  <div className="notFoundDataForChart">
                        
                  </div>
                ))}
            </div>
          </div>

          <div className="d-flex flex-column justify-content-stretch align-items-stretch miniPanel-Body mx-auto mb-5">
            <div className="d-flex flex-column justify-content-stretch align-items-stretch flex-md-row justify-content-between w-100 mb-3">
              <div className="miniPanel mb-3 bg-white w-md-45-i">
                {this.state.getPosts.length > 0 ? (
                  <Doughnut
                    data={{
                      labels: [...this.state.projectsLabels],
                      datasets: [
                        {
                          data: [...this.state.projectsCount],
                          backgroundColor: [
                            ...this.state.projectsTimeChartColor
                          ],
                          borderColor: "#fff",
                          borderWidth: 2
                        }
                      ]
                    }}
                    options={this.miniChartOptions("   ")}
                    legend={{
                      display: true,
                      position: "right",
                      align: "start",

                      labels: {
                        fontFamily: "iranSans"
                      }
                    }}
                  />
                ) : (
                  <div className="miniNotFoundDataForChart px-4">
                        
                  </div>
                )}
              </div>
              <div className="miniPanel mb-3 bg-white w-md-45-i">
                {this.state.getPosts.length > 0 ? (
                  <Doughnut
                    data={{
                      labels: [...this.state.activityLabels],
                      datasets: [
                        {
                          data: [...this.state.activityCount],
                          backgroundColor: [
                            ...this.state.activityTimeChartColor
                          ],
                          borderColor: "#fff",
                          borderWidth: 2
                        }
                      ]
                    }}
                    options={this.miniChartOptions("   ")}
                    legend={{
                      display: true,
                      position: "right",
                      align: "start",

                      labels: {
                        fontFamily: "iranSans"
                      }
                    }}
                  />
                ) : (
                  <div className="miniNotFoundDataForChart px-4">
                        
                  </div>
                )}
              </div>
            </div>

            <div className="d-flex flex-column justify-content-stretch align-items-stretch flex-md-row justify-content-between w-100">
              <div className="miniPanel mb-3 bg-white w-md-45-i">
                {this.state.getPosts.length > 0 ? (
                  <Doughnut
                    data={{
                      labels: ["Kaveh.Karami.KK"],
                      datasets: [
                        {
                          data: [this.state.userPostsTimeSum],
                          backgroundColor: ["#21335E"],
                          borderColor: "#fff",
                          borderWidth: 2
                        }
                      ]
                    }}
                    options={this.miniChartOptions("   ")}
                    legend={{
                      display: true,
                      position: "right",
                      align: "start",

                      labels: {
                        fontFamily: "iranSans"
                      }
                    }}
                  />
                ) : (
                  <div className="miniNotFoundDataForChart px-4">
                        
                  </div>
                )}
              </div>
              <div className="miniPanel mb-3 bg-white w-md-45-i">
                {/* FIXME because i didnt develope Team component, i havn't got any data to show */}
                <div className="miniNotFoundDataForChart px-4">
                      
                </div>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Dashboard;
