// FIXME Search box didn't develop
import React, { Component } from "react";
import axios from "axios";

class ReportProject extends Component {
  /* ------------------------------- Properties ------------------------------- */

  state = {
    getPosts: [],
    getProjects: [],
  };

  /* ---------------------------- lifecycle methods --------------------------- */

  componentDidMount = () => {
    this.getProjects();
    this.getPosts();
  };

  componentWillUnmount = () => {
    this.Unmount = true;
  };

  /* ---------------------------------- axios --------------------------------- */

  getPosts = () => {
    axios({
      url: "https://minit-server-app.herokuapp.com/posts",
    }).then((res) => {
      if (this.Unmount) return;
      this.setState({ getPosts: res.data });
      this.sortProjects();
    });
  };

  getProjects = () => {
    axios({
      url: "https://minit-server-app.herokuapp.com/projects",
    }).then((res) => {
      if (this.Unmount) return;
      this.setState({ getProjects: res.data });
    });
  };

  /* ------------------------------ other methods ----------------------------- */
  sortProjects = () => {
    const posts = [...this.state.getPosts];
    const projects = [...this.state.getProjects];
    const projectsArr = [];
    for (let i in posts) {
      projectsArr.push([posts[i].projectName, posts[i].projectId]);
    }

    const uniqueDate = new Set(projectsArr);

    const newProjectsArr = [...uniqueDate];

    const dataObjectForDashboardChart = {};

    for (let i in newProjectsArr) {
      dataObjectForDashboardChart[newProjectsArr[i][0]] = {
        id: newProjectsArr[i][1],
        count: 0,
      };
    }

    posts.filter((item) => {
      return newProjectsArr.some((value) =>
        item.projectName === value[0] && item.projectId === value[1]
          ? (dataObjectForDashboardChart[item.projectName]["count"] +=
              item.time.count)
          : null
      );
    });

    const newProjects = [];

    for (let i in dataObjectForDashboardChart) {
      newProjects.push({
        projectName: i,
        projectId: dataObjectForDashboardChart[i]["id"],
        count: dataObjectForDashboardChart[i]["count"],
      });
    }

    projects.filter((item) =>
      newProjects.filter((value) => {
        if (item.id === value.projectId) {
          value["clientName"] = item.clientName;
          value["budget"] = item.budget;
          value["color"] = item.color;
        }

        return 1;
      })
    );

    this.setState({ getProjects: newProjects });
  };

  formatTime = (secs) => {
    var hours = Math.floor(secs / 3600);
    var divisor_for_minutes = secs % 3600;
    var minutes = Math.floor(divisor_for_minutes / 60);
    var seconds = divisor_for_minutes % 60;

    return `${hours < 10 ? "0" + hours : hours}:${
      minutes < 10 ? "0" + minutes : minutes
    }:${seconds < 10 ? "0" + seconds : seconds}`;
  };

  render() {
    return (
      <React.Fragment>
        <div className="report-parent h-100 px-3">
          <div className="report-header"> </div>

          <div className="report-search bg-white mb-5 p-3 d-flex flex-column flex-md-row justify-content-md-between align-items-center">
            <div className="d-flex flex-column flex-sm-row justify-content-start align-items-center flex-wrap flex-md-nowrap">
              <div className="d-flex justify-content-start align-items-center px-3 report-search-items">
                <div className="pointer">
                  <input
                    type="text"
                    className="fs-13 form-control"
                    placeholder=""
                  />
                </div>
              </div>

              <div className="d-flex justify-content-start align-items-center px-3 report-search-items">
                <div className="pointer">
                  <input
                    type="text"
                    className="fs-13 form-control"
                    placeholder=""
                  />
                </div>
              </div>
            </div>

            <div className="mt-3 mt-md-0">
              <button className="btn btn-secondary">
                <i className="fas fa-search"></i>
              </button>
              <button className="btn btn-secondary mr-2">
                <i className="fas fa-times"></i>
              </button>
            </div>
          </div>

          <div className="table-responsive px-4 reportProjectTabelBox bg-white">
            <table className="table showReportTable reportProjectTabel bg-white">
              <thead>
                <tr>
                  <td></td>
                  <td></td>
                  <td> </td>
                  <td>  </td>
                </tr>
              </thead>

              <tbody>
                {this.state.getProjects.map((item, index) => {
                  return (
                    <tr key={index}>
                      <td className="pointer" style={{ color: item.color }}>
                        {item.projectName}
                      </td>
                      <td className="pointer">{item.clientName}</td>
                      <td className="pointer iranSansNum">{item.budget}</td>

                      <td className="pointer iranSansNum">
                        {this.formatTime(item.count)}
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default ReportProject;
