import React from "react";

const EmptyPost = () => {
  return (
    <div className="empty-show d-flex flex-column align-items-center">
      <div className="mt-120 mb-60">
        <div className="empty-show-item">
          <i className="far fa-hand-point-up"></i>
          <span>       !</span>
        </div>

        <div className="empty-show-item">
          <i className="far fa-hand-point-right"></i>
          <span>        .</span>
        </div>

        <div className="empty-show-item">
          <i className="far fa-hand-point-left rotate-27"></i>
          <span>
                         .
          </span>
        </div>
      </div>
    </div>
  );
};

export default EmptyPost;
